.fx_menuItem {
  display: flex;
  align-items: center;
}
.fx_menuTitle {
  height: 70px;
  line-height: 70px;
  padding-left: 26px;
  background: #ffffff;
  font-size: 18px;
  font-family: PingFangSC, PingFangSC-Medium;
  font-weight: 500;
  color: #333333;
}
.fx_menuTitle span {
  width: 5px;
  height: 28px;
  background: #267cff;
  border-radius: 3px;
  margin-right: 16px;
  display: inline-block;
  vertical-align: middle;
}
.fx_content {
  padding: 16px;
  min-height: calc(-80vh);
}
